import React, { Component } from 'react';
import './styles.css';

class NewsItem extends Component {
  render() {
    const { data } = this.props;
    return (
      <React.Fragment>
        <img className="card-img-top" src={data.image} alt={data.title} />
        <div className="card-body">
          <h5 className="card-title">{data.title}</h5>
          <p className="card-text">{data.shortDescription}</p>
        </div>
        <div className="card-footer">
          <small className="text-muted">Last updated {data.lastUpdated}</small>
          <div className="fb-share-button" 
               data-href={data.link} 
               data-layout="button_count" 
               data-size="small" 
               data-mobile-iframe="true"
          >
            <a target="_blank"
               className="fb-xfbml-parse-ignore">Поделиться</a>
          </div>
        </div>
      </React.Fragment>
    )
  }
}

export default NewsItem;