import React, { Component } from 'react';
import './styles.css';

import PhotoList from '../photoList/photoList.js';
import VideoList from '../videoList/videoList.js';

class Sidebar extends Component {
  render() {
    const { 
      teamData: { photos, videos }
    } = this.props;
    return (
      <aside className="col-12 col-md-4 order-md-1 px-1 align-items-center">
        <PhotoList 
          quantity={4}
          photoData={photos}
        />
        <VideoList 
          quantity={2}
          videoData={videos}
        />
      </aside>
    )
  }
}

export default Sidebar;