import React, { Component } from 'react';
import './styles.css';
import VideoItem from '../../components/videoItem/videoItem.js';

class VideoList extends Component {

  render() {
    const { quantity, videoData } = this.props;
    return (
      <div className="col-12 px-0 my-3 s-videoShortList">  
        <div className="d-flex justify-content-center s-video-title">
          <h5>Видео ({videoData.length})</h5>
        </div>
        <div className="d-flex flex-column p-1 s-videoListBlock">
          <div className="d-flex flex-column s-videoList">
            { quantity !== videoData.length
              ? (videoData.slice(0, quantity)).map((video)=> (
                  <div className="col-12 px-0" key={video.id} id={video.id}>
                    <VideoItem
                      data={video}
                    />
                  </div>
                ))
              : <span>нет видео</span>
            }
          </div>
          <div className="d-flex justify-content-center align-items-center mt-2 mb-1 s-videoListLink">
            <a href="#">все видео</a>
          </div>
        </div>
      </div>
    )
  }
}

export default VideoList;