import React, { Component } from 'react';
import './styles.css';

class PartnerItem extends Component {
  render() {
    const { data } = this.props;
    return (
      <div className="col-2 px-1 py-2 partnerItem">
        <img src={data.link} className="partner-logo" alt={data.name} />
      </div>
    )
  }
}

export default PartnerItem;