import React, { Component } from 'react';
import './styles.css';
import PhotoItem from '../../components/photoItem/photoItem.js';

class PhotoList extends Component {
  toggleBigImage = (originalImgLink, originalImgName) => {
    console.log('1');
    this.setState({ 
      bigImage: (this.state.bigImage === 'none') ? 'flex' : 'none',
      originalImgLink: originalImgLink,
      originalImgName: originalImgName,
      })
  }

  render() {
    const { quantity, photoData } = this.props;
    return (
      <div className="col-12 px-0 my-3 s-photoShortList">  
        <div className="d-flex justify-content-center s-photo-title">
          <h5>Фото ({photoData.length})</h5>
        </div>
        <div className="d-flex flex-column p-1 s-photoListBlock">
          <div className="d-flex s-photoList">
            { quantity !== photoData.length
              ? (photoData.slice(0, quantity)).map((photo)=> (
                  <div className=" col-3 px-0" key={photo.id} id={photo.id}>
                    <PhotoItem
                      data={photo}
                    />
                  </div>
                ))
              : <span>нет фото</span>
            }
          </div>
          <div className="d-flex justify-content-center align-items-center mt-2 mb-1 s-photoListLink">
            <a href="#">все фотографии</a>
          </div>
        </div>
      </div>
    )
  }
}

export default PhotoList;